import Vue from 'vue'
import Vuex from 'vuex'
import usersAPI from "./../apis/users";
import { Toast } from "./../utils/helpers";


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentUser: {
      id: -1,
      name: '',
      introduction: '',
      avatar: '',
      cover_image: '',
      isAdmin: false
    },
    isAuthenticated: false,
    //  token 
    token: ''
  },
  getters: {
  },
  mutations: {
    setCurrentUser(state, currentUser) {
      state.currentUser = {
        ...state.currentUser,
        //  API  currentUser  Vuex state  currentUser
        ...currentUser
      }
      //  token  state 
      state.token = localStorage.getItem('token')
      //  true
      state.isAuthenticated = true
    },
    revokeAuthentication(state) {
      state.currentUser = {}
      state.isAuthenticated = false
      //  state  token 
      state.token = ''
      localStorage.removeItem('token')
    }
  },
  actions: {
    async fetchCurrentUser({ commit }) {
      try {
        const response = await usersAPI.getCurrentUser()
        // console.log('fetchCurrentUser', response)

        const { account, avatar, cover_image, folloingCount, followerCount, id, introduction, name, tweetCount } = response.data.user

        commit('setCurrentUser', {
          account, avatar, cover_image, folloingCount, followerCount, id, introduction, name, tweetCount
        })
        return true
      } catch (error) {
        console.log('can not fetch current user information, if you are an admin, it is ok')
        //  state  token
        // commit('revokeAuthentication')
        // return false
        return true
      }
    }

  },

  modules: {
  }
})
